@import '../../styles/theme';

.participate {
  &-container {
    .notAssigned {
      padding-top: 0;
    }

    hr {
      margin-bottom: 1rem;
    }

    .row {
      padding-bottom: 0.5rem;
    }

    & > section {
      padding-top: 1rem;

      &:first-of-type {
        padding-top: 0;
      }
    }

    .station {
      @media (max-width: 768px) {
        .row {
          flex-direction: column;
        }
      }

      &__header {
        margin-bottom: 0.5rem;
        color: #ff8d33;
      }
    }

    .admonition {
      padding: 1rem;
      color: rgba(#fff, 0.9);

      &--danger {
        background-color: lighten(red, 20);
        border-left: 5px solid darken(red, 3);
      }

      &--warning {
        background-color: lighten($primaryColor, 15);
        border-left: 5px solid darken($primaryColor, 3);
      }

      &__title {
        font-weight: bold;
        font-size: 1.2rem;
      }

      p {
        margin-top: 0;
      }
    }

    .task {
      &__header {
        &--hint {
          display: inline-block;
          text-align: center;
          background: $primaryColor;
          height: 1rem;
          width: 1rem;
          padding: 0.25rem;
          margin-left: 1rem;
          border-radius: 50%;
          font-size: 0.9rem;
          transition: background 0.25s ease-in-out;
          cursor: pointer;

          &:hover,
          &--active {
            background: $ascentColor;
          }
        }
      }

      &__description {
        line-height: 1.5rem;
        code {
          background: lighten($foregroundColor, 25%);
          padding: 0 0.5rem;
          border-radius: 3px;
        }
      }

      &__hint {
        &--hidden {
          display: none;
        }
      }

      &__test {
        margin: auto;
        padding: 0.5rem;
        margin-bottom: 0.25rem;
        border-radius: 3px;
        position: relative;
        cursor: pointer;

        &:after {
          content: '<';
          position: absolute;
          right: 1rem;
          transform: rotate(90deg);
          transition: transform 0.1s ease-in-out;
          font-weight: bold;
        }

        &--expanded {
          &:after {
            transform: rotate(270deg);
          }
        }

        & > *:first-child {
          text-transform: capitalize;
        }

        &--ok {
          background: #39dd6d;
          color: #3d3d3d;
        }

        &--skipped {
          background: #f7b538;
          color: #3d3d3d;
        }

        &--fail {
          background: #dd2c32;
          color: #3d3d3d;
        }

        &-description {
          padding-bottom: 2rem;

          display: none;

          &--active {
            display: block;
          }
        }
      }
    }
  }
}
